<template>
  <div id="app">
    <div class="header" style="z-index: 3">
      <ul class="header-button-left">
        <li @click="clickCancel">Cancel</li>
      </ul>
      <ul class="header-button-right">
        <li v-if="step === 1" @click="plusStep()">Next</li>
        <li v-if="step === 2" @click="publish">발행</li>
      </ul>
      <img src="./assets/logo.png" class="logo" />
    </div>
    <br />
    <button v-if="step === 0" @click="$store.dispatch('getData')">더보기버튼</button>
    <button v-if="step !== 3" @click="$store.commit('gotoMypage')">마이페이지</button>

    <Container @write="writeContent = $event" />

    <!-- <button @click="more">더보기</button> -->

    <div class="footer">
      <ul class="footer-button-plus">
        <input @change="upload" type="file" id="file" class="inputfile" />
        <label for="file" class="input-plus">+</label>
      </ul>
    </div>
  </div>
</template>

<script>
import Container from "./components/Container.vue";
import { mapState, mapMutations } from "vuex";

export default {
  name: "App",
  mounted() {
    this.emitter.on("filtered", (filtered) => {
      this.filtered = filtered;
    });
  },
  computed: {
    ...mapState(["data", "mydata", "step"]),
  },
  methods: {
    ...mapMutations(["plusStep", "postWriting", "setContent", "clickCancel"]),
    // more() {
    //   axios
    //     .get(`https://codingapple1.github.io/vue/more${this.cnt}.json`)
    //     .then((res) => {
    //       this.data.unshift(res.data);
    //       this.cnt++;
    //     })
    //     .catch((err) => {
    //       console.log(err);
    //     });
    // },
    upload(e) {
      let file = e.target.files;
      this.$store.commit("setPostImage", URL.createObjectURL(file[0]));
      this.plusStep();
    },
    publish() {
      this.postWriting(this.mydata);
      this.plusStep();
    },
  },
  components: {
    Container: Container,
  },
};
</script>

<style>
body {
  margin: 0;
}
ul {
  padding: 5px;
  list-style-type: none;
}
.logo {
  width: 22px;
  margin: auto;
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  top: 13px;
}
.header {
  width: 100%;
  height: 40px;
  background-color: white;
  padding-bottom: 8px;
  position: sticky;
  top: 0;
}
.header-button-left {
  color: skyblue;
  float: left;
  width: 50px;
  padding-left: 20px;
  cursor: pointer;
  margin-top: 10px;
}
.header-button-right {
  color: skyblue;
  float: right;
  width: 50px;
  cursor: pointer;
  margin-top: 10px;
}
.footer {
  width: 100%;
  position: sticky;
  bottom: 0;
  padding-bottom: 10px;
  background-color: white;
}
.footer-button-plus {
  width: 80px;
  margin: auto;
  text-align: center;
  cursor: pointer;
  font-size: 24px;
  padding-top: 12px;
}
.sample-box {
  width: 100%;
  height: 600px;
  background-color: bisque;
}
.inputfile {
  display: none;
}
.input-plus {
  cursor: pointer;
}
#app {
  box-sizing: border-box;
  font-family: "consolas";
  margin-top: 60px;
  width: 100%;
  max-width: 460px;
  margin: auto;
  position: relative;
  border-right: 1px solid #eee;
  border-left: 1px solid #eee;
}
</style>
